  methods: {
    getRand() {
        const date = new Date();
        let a = date.getFullYear() * 365 * 24 * 60 * 60 * 1000;
        let b = date.getMonth() * a;
        let c = date.getDate() * b;
        let d = date.getDay() * c;

        a >>>= 0; b >>>= 0; c >>>= 0; d >>>= 0; 
        var t = (a + b) | 0;
        a = b ^ b >>> 9;
        b = c + (c << 3) | 0;
        c = (c << 21 | c >>> 11);
        d = d + 1 | 0;
        t = t + d | 0;
        c = c + t | 0;
        return (t >>> 0) / 4294967296;
    }
